@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;700&family=Staatliches&display=swap);
* {
  text-decoration: none;
  color: #000;
}
.movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: flex-start;
}
.movie-link {
  margin: 50px 10px;
  width: 40%;
}
@media screen and (max-width: 900px) {
  .movies {
    flex-direction: column;
    justify-content: center;
  }
  .movies-link {
    width: 80%;
  }
}
.movie {
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
  padding: 20px;
  min-height: 50%;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.2);
}
.movie:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}
.movie__poster {
  position: relative;
  top: -50px;
  margin-right: 30px;
  max-width: 150px;
  height: auto;
  box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.2);
}
.movie__data {
  width: 70%;
}
.movie__title {
  margin: 10px 0 0;
  font-family: "Staatliches", cursive;
  font-size: 24px;
}
.movie__year {
  display: inline;
  margin: 15px 0 0;
  font-size: 18px;
  font-weight: 700;
}
.movie__rating {
  margin-left: 10px;
  font-size: 14px;
  color: rgb(214, 154, 0);
}
.movie__genres {
  display: flex;
  margin: 10px 0 0;
  padding: 0;
  color: rgb(119, 119, 119);
}
.genres__genre {
  margin: 0 5px 0 0;
  list-style: none;
  font-size: 16px;
}
.genres__genre::after {
  content: " · ";
}
.genres__genre:last-child::after {
  display: none;
}
.movie__summary {
  margin: 10px 0 0;
  font-size: 14px;
  color: rgb(119, 119, 119);
  line-height: 150%;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: rgb(220, 234, 247);
  font-family: "Raleway", sans-serif;
  font-weight: 500;
}

html,
body,
#root,
.container {
  height: 100%;
}
/* ------------ loading ---------- */
.loader {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loader__text {
  border-right: 0.15em solid #fff;
  letter-spacing: 0.15em;
  -webkit-animation: blink-caret 0.7s step-end infinite;
          animation: blink-caret 0.7s step-end infinite;
}
@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #fff;
  }
}
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #fff;
  }
}

.about__container {
  display: flex;
  justify-content: center;
}
.about__container span {
  margin-top: 20px;
  padding: 20px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.1);
}

* {
  box-sizing: border-box;
}

.movie-detail {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px;
  padding: 30px;
  max-width: 60%;
  max-height: 80%;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.2);
}
.movie-detail__poster {
  max-width: 500px;
  height: auto;
  box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.2);
}
.movie-detail__data {
  text-align: center;
}
.movie-detail__title {
  margin: 30px 0 0;
  font-family: "Staatliches", cursive;
  font-size: 36px;
}
.movie-detail__year {
  display: inline;
  margin: 15px 0 0;
  font-size: 24px;
  font-weight: 700;
}
.movie-detail__rating {
  display: block;
  margin: 15px 0 0;
  font-size: 14px;
  color: rgb(214, 154, 0);
}
.movie-detail__genres {
  display: flex;
  justify-content: center;
  margin: 15px 0 0;
  padding: 0;
}
.genres__genre {
  margin: 0 5px 0 0;
  list-style: none;
  font-size: 14px;
  color: rgb(85, 85, 85);
}
.genres__genre::after {
  content: " · ";
  color: rgb(85, 85, 85);
}
.genres__genre:last-child::after {
  display: none;
}
.movie-detail__summary {
  margin: 30px 30px 0;
  font-size: 14px;
  color: rgb(119, 119, 119);
  line-height: 150%;
}
.movie-detail__back {
  display: block;
  text-align: center;
  margin: 40px 0 10px;
  padding: 10px 20px;
  border: 1px solid rgb(165, 167, 170);
  border-radius: 10px;
  text-decoration: none;
  color: rgb(110, 110, 110);
}
.movie-detail__back:hover {
  color: #fff;
  background-color: rgb(165, 167, 170);
}

.nav-bar {
  margin-bottom: 20px;
  padding: 13px 10px;
  padding-left: 20px;
  background-color: #fff;
  box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.1);
}
.nav__menu {
  text-decoration: none;
  color: rgb(59, 59, 59);
}
.nav__menu:hover {
  color: rgb(138, 179, 255);
}
.nav__menu::after {
  margin: 0 10px;
  content: " |";
  color: rgba(0, 0, 0, 0.3);
}
.nav__menu:last-child::after {
  display: none;
}

